import merge from "deepmerge"
import defaultStyle from "gatsby-theme-blog/src/gatsby-plugin-theme-ui/styles"


export default merge(defaultStyle, {
  blockquote: {
    color: `inherit`,
    borderLeft: `5px solid`,
    borderLeftColor: `text`,
    padding: '0 2em',
    marginLeft: 0,
    marginRight: 0,
    opacity: 0.8,
    "&.translation": {
      fontSize: `1em`,
    },
  },
})
