import merge from "deepmerge"
import defaultPrism from "gatsby-theme-blog/src/gatsby-plugin-theme-ui/prism"


export default merge(defaultPrism, {
  ".token.attr-name": {
    color: '#addb67'
  },
  ".token.unit": {
    color: `prism.number`
  },
  ".token.comment": {
    color: '#01fd1d'
  }
})
